<template>
    <div class="module-info">
        <div class="field-part">
            <!-- <slot name="item"></slot> -->
            <cell title="缴费项目">
                <span slot="value" :class=" 'icon icon-menu-' + icon "></span>
                <span slot="desc" class="type-desc">{{value}}</span>
            </cell>
        </div>

        <div class="field-part">
            <cell-map :dataCopy="infoList" :map="map"></cell-map>
        </div>

        <div class="field-part">
            <mt-cell title="缴费金额">
                <span class="readonly-amount">{{infoList.PAYAMT | formatNumber(2)}}元</span>
            </mt-cell>
        </div>

    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    export default {
        name: 'module-info',
        props: ['module'],
        data() {
            return {
                infoList: {},
                map: [
                    {
                        key: 'COMPANY_NAME',
                        title: '供电公司'
                    },
                    {
                        key: 'USERNO',
                        title: '用户编号'
                    },
                    {
                        key: 'USERNAME',
                        title: '用户姓名'
                    },
                    {
                        key: 'PAYAMT',
                        type: 'amount-unit',
                        title: '应缴金额'
                    }
                ]
            };
        },
        computed: {
            ...mapGetters(['selfMap', 'lifeMap']),
            value() {
                let m = this.selfMap[this.module];
                return this.lifeMap[m].title;
            },
            icon() {
                let m = this.selfMap[this.module];
                return this.lifeMap[m].no;
            }
        },
        created() {
            let ls = this.$ls.getItem(this.module);
            this.infoList = ls;
        }
    };

</script>

<style lang="scss" scoped>
    .readonly-amount {
        font-size: 20px;
        color: #FF942F;
        letter-spacing: 0;
    }

    .icon {
        position: initial;
        top: initial;
        left: initial;
        transform: initial;
        display: inline-block;
        width: 44px;
        height: 44px;
        vertical-align: middle;
    }

    .type-desc {
        vertical-align: middle;
    }
</style>


