<template>
    <div class="module">
        <div class="field-part">
            <cell-map :dataCopy="infoList" :map="map"></cell-map>
        </div>

        <div class="field-part">
            <account-select @click.native="showAccount" title="支付账户" :value="formData.account" :balance="formData.account_balance"></account-select>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-warn" v-if="!balanceWarn.status">
            {{balanceWarn.text}}
        </div>

        <div class="field-part">
            <mt-field label="交易密码" placeholder="请输入交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-warn" v-if="!passwordError.status">
            {{passwordError.text}}
        </div>

        <div class="field-action button-bottom">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="doPay">立即支付</mt-button>
        </div>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    export default {
        name: 'module',
        data() {
            return {
                infoList: '',
                map: [
                    {
                        key: 'call',
                        title: '手机号码'
                    },
                    {
                        key: 'info',
                        title: '运营商',
                        divide: true
                    },
                    {
                        key: 'amount',
                        type: 'amount-unit',
                        title: '支付金额'
                    }
                ],
                formData: {
                    account: '',
                    account_balance: '',
                    pwd: ''
                },
                balanceWarn: {
                    status: true,
                    text: ''
                },
                passwordError: {
                    status: true,
                    text: ''
                },
                clickable: false,
                dialogSmsVerify: {
                    title: '输入短信验证码',
                    btnConfirmText: '确定转账',
                    btnGetText: '获取验证码'
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    checked: {},
                    showAddBtn: true,
                    accountList: [
                        {
                            id: 4,
                            name: '郭振宇',
                            alias: '郭振宇',
                            ACCOUNT: '625228***0089',
                            ACCOUNT_BALANCE: '1342.00',
                            BANKNAME: '安徽农金',
                            ISDEFAULT: true
                        },
                        {
                            id: 5,
                            name: '郭振宇2',
                            alias: '郭振宇2',
                            ACCOUNT: '625228***0025',
                            ACCOUNT_BALANCE: '42.00',
                            BANKNAME: '安徽农金'
                        },
                        {
                            id: 6,
                            name: '郭振宇3',
                            alias: '',
                            ACCOUNT: '625228***0109',
                            ACCOUNT_BALANCE: '12942.00',
                            BANKNAME: '安徽农金'
                        }
                    ]
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('phone');
            this.infoList = ls;
            let _data = this.selectorPaymentAccount.accountList.filter(item => {
                return item.ISDEFAULT === true;
            });
            this.formData.account = _data[0].ACCOUNT;
            this.formData.account_balance = _data[0].ACCOUNT_BALANCE;
        },
        watch: {
            'formData': {
                handler: 'validate',
                deep: true
            }
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.ACCOUNT;
                    this.formData.account_balance = this.selectorPaymentAccount.checked.ACCOUNT_BALANCE;
                    this.formData.pwd = "";
                }, (data) => {
                });
            },
            doPay() {
                let _data = Object.assign({}, this.formData);
                this.$refs.dialogSmsVerify.show().then((data) => {
                    alert('短信验证码验证成功...');
                    this.$router.push({ name: 'LifeResult', query: { p: -3 } });
                }, (data) => {
                });
            },
            validate(val) {
                if (this.$validate.isEmpty(val.account)) {
                    this.clickable = false;
                    return false;
                }
                if (Number(val.account_balance) < Number(this.infoList.amount)) {
                    this.balanceWarn.text = '付款账号可用余额不足';
                    this.balanceWarn.status = false;
                } else {
                    this.balanceWarn.text = '';
                    this.balanceWarn.status = true;
                }
                if (this.balanceWarn.status === false) {
                    this.clickable = false;
                    return false;
                }
                if (this.$validate.isEmpty(val.pwd)) {
                    this.clickable = false;
                    return false;
                }
                this.clickable = true;
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('phone');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .account-balance {
        color: #e6ad57;
        margin-top: 10px;
    }
</style>


