<template>
    <div class="power-do-pay">
        <div class="field-part">
            <!-- <slot name="item"></slot> -->
            <cell title="缴费项目">
                <span slot="value" :class=" 'icon icon-menu-' + icon "></span>
                <span slot="desc" class="type-desc">{{value}}</span>
            </cell>
        </div>

        <div class="field-part">
            <mt-cell title="支付金额">{{infoList.money | formatNumber(2)}}元</mt-cell>
        </div>

        <div class="field-part">
            <account-select title='支付账号' :value='formData.account' :balance="formData.account_balance" @click.native="showAccount()"></account-select>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>
        <div class="field-warn" v-if="!balanceWarn.status">
            {{balanceWarn.text}}
        </div>

        <div class="field-part">
            <mt-field label="交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-warn" v-if="!passwordError.status">
            {{passwordError.text}}
        </div>

        <div class="field-part">
            <mt-field label="备注名称" v-model="formData.mark"></mt-field>
        </div>

        <div class="field-hint">
            {{warn}}
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    export default {
        name: 'power-do-pay',
        props: {
            warn: [String],
            module: [String]
        },
        data() {
            return {
                infoList: {},
                formData: {
                    account: '',
                    account_balance: '',
                    pwd: '',
                    mark: ''
                },
                selectorPaymentAccount: {
                    checked: {},
                    showAddBtn: true,
                    showWarn: true,
                    accountList: [
                        {
                            id: 4,
                            name: '郭振宇',
                            ALIAS: '郭振宇',
                            ACCOUNT: '625228***0089',
                            ACCOUNT_BALANCE: '45000.67',
                            BANKNAME: '安徽农金',
                            ISDEFAULT: true
                        },
                        {
                            id: 5,
                            name: '郭振宇2',
                            ALIAS: '郭振宇2',
                            ACCOUNT: '625228***0025',
                            ACCOUNT_BALANCE: '23.67',
                            BANKNAME: '安徽农金'
                        },
                        {
                            id: 6,
                            name: '郭振宇3',
                            ALIAS: '',
                            ACCOUNT: '625228***0109',
                            ACCOUNT_BALANCE: '26000.67',
                            BANKNAME: '安徽农金'
                        }
                    ]
                },
                balanceWarn: {
                    status: true,
                    text: ''
                },
                passwordError: {
                    status: true,
                    text: ''
                }
            };
        },
        watch: {
            'formData': {
                handler: 'validate',
                deep: true
            }
        },
        computed: {
            ...mapGetters(['selfMap', 'lifeMap']),
            value() {
                let m = this.selfMap[this.module];
                return this.lifeMap[m].title;
            },
            icon() {
                let m = this.selfMap[this.module];
                return this.lifeMap[m].no;
            }
        },
        created() {
            let ls = this.$ls.getItem(this.module);
            this.infoList = Object.assign({}, ls);
            let _data = this.selectorPaymentAccount.accountList.filter(item => {
                return item.ISDEFAULT === true;
            });
            this.formData.account = _data[0].ACCOUNT;
            this.formData.account_balance = _data[0].ACCOUNT_BALANCE;
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.ACCOUNT;
                    this.formData.account_balance = this.selectorPaymentAccount.checked.ACCOUNT_BALANCE;
                    this.formData.pwd = '';
                }, (data) => {
                });
            },
            validate(val) {
                if (this.$validate.isEmpty(val.account)) {
                    this.$emit('validate', false);
                    return false;
                }
                if (Number(val.account_balance) < Number(this.infoList.money)) {
                    this.balanceWarn.text = '付款账号可用余额不足';
                    this.balanceWarn.status = false;
                } else {
                    this.balanceWarn.text = '';
                    this.balanceWarn.status = true;
                }
                if (this.balanceWarn.status === false) {
                    this.$emit('validate', false);
                    return false;
                }
                if (this.$validate.isEmpty(val.pwd)) {
                    this.$emit('validate', false);
                    return false;
                }
                this.$emit('validate', true);
            }
        }
    };

</script>

<style lang="scss" scoped>
    .select-account {
        background-color: #fff;
    }

    .account-balance {
        font-size: 13px;
        color: #666666;
        letter-spacing: 0;
        margin-top: 16px;

        span {
            color: #FF942F;
            padding: 0 10px;
        }
    }

    .icon {
        position: initial;
        top: initial;
        left: initial;
        transform: initial;
        display: inline-block;
        width: 44px;
        height: 44px;
        vertical-align: middle;
    }

    .type-desc {
        vertical-align: middle;
    }

    .warn-wrap {
        margin: 16px 0 20px 50px;
        font-size: 13px;
        color: #666;
    }
</style>


