<template>
    <div class="traffic-pay">
        <div class="field-part">
            <mt-cell title="支付金额">{{formData.money | formatNumber(2)}}元</mt-cell>
        </div>

        <div class="field-part select-account">
            <account-select title='支付账号' :value='formData.account' :balance="formData.account_balance" @click.native="showAccount()"></account-select>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-warn" v-if="!balanceWarn.status">
            {{balanceWarn.text}}
        </div>

        <div class="field-part">
            <mt-field label="交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-warn" v-if="!passwordError.status">
            {{passwordError.text}}
        </div>

        <div class="field-action button-bottom">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="doPay">立即支付</mt-button>
        </div>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    export default {
        name: 'traffic-pay',
        data() {
            return {
                formData: {
                    money: '',
                    id: '',
                    account: '',
                    account_balance: '',
                    pwd: '',
                    mark: ''
                },
                clickable: false,
                balanceWarn: {
                    status: true,
                    text: ''
                },
                passwordError: {
                    status: true,
                    text: ''
                },
                dialogSmsVerify: {
                    title: '输入短信验证码',
                    btnConfirmText: '确定转账',
                    btnGetText: '获取验证码'
                },
                selectorPaymentAccount: {
                    title: '选择支付账号',
                    checked: {},
                    showAddBtn: true,
                    accountList: [
                        {
                            id: 1,
                            name: '郭振宇',
                            ACCOUNT: '625228***0089',
                            ACCOUNT_BALANCE: '15000.67',
                            ISDEFAULT: true
                        },
                        {
                            id: 2,
                            name: '郭振宇2',
                            ACCOUNT: '625228***0025',
                            ACCOUNT_BALANCE: '40.67'
                        },
                        {
                            id: 3,
                            name: '郭振宇3',
                            ALIAS: '',
                            ACCOUNT: '625228***0109',
                            ACCOUNT_BALANCE: '20000.67',
                            BANKNAME: '安徽农金'
                        }
                    ]
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('traffic');
            this.formData.money = ls.total_amount;
            let _data = this.selectorPaymentAccount.accountList.filter(item => {
                return item.ISDEFAULT === true;
            });
            this.formData.account = _data[0].ACCOUNT;
            this.formData.account_balance = _data[0].ACCOUNT_BALANCE;
        },
        watch: {
            'formData': {
                handler: 'validate',
                deep: true
            }
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.id = this.selectorPaymentAccount.checked.id;
                    this.formData.account = this.selectorPaymentAccount.checked.ACCOUNT_BALANCE;
                    this.formData.account_balance = this.selectorPaymentAccount.checked.ACCOUNT_BALANCE;
                    this.formData.pwd = "";
                }, (data) => {
                });
            },
            doPay() {
                this.$refs.dialogSmsVerify.show().then((data) => {
                    this.$router.push({ name: 'LifeResult', query: { p: -4 } });
                }, (data) => {
                });
            },
            validate(val) {
                if (this.$validate.isEmpty(val.account)) {
                    this.clickable = false;
                    return false;
                }
                if (Number(val.account_balance) < Number(val.money)) {
                    this.balanceWarn.text = '付款账号可用余额不足';
                    this.balanceWarn.status = false;
                } else {
                    this.balanceWarn.text = '';
                    this.balanceWarn.status = true;
                }
                if (this.balanceWarn.status === false) {
                    this.clickable = false;
                    return false;
                }
                if (this.$validate.isEmpty(val.pwd)) {
                    this.clickable = false;
                    return false;
                }
                this.clickable = true;
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('traffic');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .select-account {
        background-color: #fff;
    }

    .account-balance {
        color: #e6ad57;
        margin-top: 10px;
    }
</style>


