<template>
    <div class="history-detail">
        <div class="main">
            <div class="hd">
                <div class="title">
                    <i slot="icon" :class=" 'icon icon-menu-' + no "></i>
                    <p v-text="title"></p>
                </div>
                <div class="amount">
                    <p>{{detailList.pay_amount | formatNumber(2)}}元</p>
                </div>
            </div>
            <div class="wrap">
                <cell-map class="mt-cell" :data-copy="detailList" :map="filter"></cell-map>
            </div>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    import { mapGetters } from 'vuex';
    import Map from './map';
    export default {
        name: 'history-detail',
        data() {
            return {
                detailList: {},
                no: null
            };
        },
        computed: {
            ...mapGetters(['lifeMap']),
            title() {
                if (this.detailList.type === '交通罚款') {
                    return '交通罚款';
                } else if (this.detailList.type === '话费充值') {
                    return this.detailList.carrieroperator;
                } else {
                    return this.detailList.busi_no;
                }
            },
            filter() {
                if (this.detailList.type === '交通罚款') {
                    return Map.traffic;
                } else if (this.detailList.type === '话费充值') {
                    return Map.phone;
                } else {
                    return Map.other;
                }
            }
        },
        created() {
            services.getHistoryListDetail({ showLoading: '加载中...' })
                .then(res => {
                    let data = res.data.filter(item => {
                        return item.tran_no === this.$route.params.id;
                    });
                    this.detailList = data[0];
                    this.no = this.lifeMap[data[0].type].no;
                    this.detailList.type = this.lifeMap[this.detailList.type].title;
                });
        }
    };

</script>

<style lang="scss" scoped>
    .main {
        .hd {
            padding: 60px 0 50px;
            background-color: #fff;
            .title {
                text-align: center;

                img {
                    vertical-align: middle;
                    display: inline-block;
                }

                p {
                    display: inline-block;
                    vertical-align: middle;
                    font-size: 17px;
                    color: #272727;
                }

                .icon {
                    position: initial;
                    top: initial;
                    left: initial;
                    transform: initial;
                    display: inline-block;
                    width: 44px;
                    height: 44px;
                    vertical-align: bottom;
                    margin-left: 10px;
                    margin-right: 10px;
                }
            }

            .amount {
                margin: 20px 0 0;
                text-align: center;

                p {
                    font-size: 24px;
                    color: #FF942F;
                }
            }
        }

        .hd+.wrap {
            margin-top: 40px;
        }
    }
</style>


