<template>
    <div class="result">
        <div class="field-result">
            <div class="result-icon"></div>

            <div class="result-name" v-text="call.status"></div>
            <div class="result-msg" v-html="call.msg"></div>
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="finish">完成</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    export default {
        name: 'result',
        data() {
            return {
                call: {
                    status: '交易成功',
                    msg: '缴费申请已受理<br>具体到账时间以合作方系统为主'
                }
            };
        },
        methods: {
            finish() {
                this.$router.go(this.$route.query.p);
            }
        }

    };

</script>

<style lang="scss" scoped>
    .field-result {
        padding: 48px 0 40px;
        text-align: center;
        background-color: #fff;

        .result-icon {
            height: 120px;
            width: 120px;
            margin: 0 auto 40px;
            @include image-set('../../assets/img/tip/success');
        }

        .result-name {
            font-size: 17px;
            color: #272727;
        }
        .result-msg {
            font-size: 13px;
            color: #B5B5B5;
            margin-top: 10px;
        }
    }

    .field-action {
        margin-top: 50px;
    }
</style>


