<template>
    <div class="power">
        <module-index ref="child" @validate="validateCall" module="power" moduleCompany="供电公司"></module-index>

        <div class="field-action button-bottom">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="doQuery">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleIndex from '../module/index';
    import * as services from '@/services/life';
    export default {
        name: 'power',
        components: {
            ModuleIndex
        },
        data() {
            return {
                clickable: false
            };
        },
        methods: {
            doQuery() {
                /** 获取缴费信息 **/
                let life = this.$refs.child.life;
                services.getPowerinfo({
                    // showLoading: '请等待...',
                    BUSINO: life.COMPANY_WHOLECODE,
                    GRIDID: life.COMPANY_CODE,
                    USERNO: life.COMPANY_WHOLECODE,
                    CITYCODE: life.AREA_CODE
                }).then(res => {
                    let _data = res.data;
                    let ls = this.$refs.child.life;
                    let obj = Object.assign({}, ls, _data);
                    this.$ls.setItem('power', obj);
                    this.$router.push({ name: 'LifePowerInfo' });
                }, error => {
                    this.$alert(error);
                });
            },
            validateCall(data) {
                this.clickable = data;
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                let ls = this.$ls.getItem('power');
                let obj = Object.assign({}, this.$refs.child.life, ls);
                this.$ls.setItem('power', obj);
            } else {
                this.$ls.removeItem('power');
            }
            next(true);
        }
    };

</script>


