<template>
    <div class="power-do-pay">
        <module-pay ref="child" @validate="validateCall" :warn="warn" module="power"></module-pay>

        <div class="field-action button-bottom">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="doPay">立即支付</mt-button>
        </div>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    import ModulePay from '../module/pay';
    export default {
        name: 'power-do-pay',
        components: {
            ModulePay
        },
        data() {
            return {
                warn: '备注名称例如：妈妈家的电费',
                dialogSmsVerify: {
                    title: '输入短信验证码',
                    btnConfirmText: '完成支付',
                    btnGetText: '获取验证码'
                },
                clickable: false
            };
        },
        methods: {
            doPay() {
                this.$refs.dialogSmsVerify.show().then((data) => {
                    let history = this.$route.query.history || -4;
                    this.$router.push({ name: 'LifeResult', query: { p: history } });
                }, (data) => {
                });
            },
            validateCall(val) {
                this.clickable = val;
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('power');
            }
            next(true);
        }
    };

</script>


