<template>
    <div class="manager">
        <div class="field-part">
            <cell-map :dataCopy="formData" :map="map"></cell-map>
        </div>
        <div class="field-part">
            <mt-field label="备注名称" v-model="formData.REASON"></mt-field>
        </div>
        <div class="field-hint">
            备注名称例如：妈妈家的{{type}}
        </div>
        <!-- 按钮 -->
        <div class="field-action button-bottom">
            <mt-button type="primary" size="large" @click="doSave">保存</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    import { mapGetters } from 'vuex';
    export default {
        name: 'manager',
        data() {
            return {
                formData: {},
                map: [
                    {
                        key: 'COMPANY_NAME',
                        title: '缴费公司'
                    },
                    {
                        key: 'USER_NO',
                        title: '用户编号'
                    },
                    {
                        key: 'USER_NE',
                        title: '用户姓名'
                    }
                ]
            };
        },
        computed: {
            ...mapGetters(['lifeMap']),
            type() {
                if (this.formData.PAYMENT_TYPE) {
                    return this.lifeMap[this.formData.PAYMENT_TYPE].title;
                } else {
                    return '电费';
                }
            }
        },
        created() {
            let ls = this.$ls.getItem('life-offen-list');
            this.formData = Object.assign({}, ls);
        },
        methods: {
            doSave() {
                this.$router.go(-1);
            }
        }
    };

</script>

<style lang="scss" scoped>

</style>


