<template>
    <div class="phone">
        <div class="field-group">
            <mt-field class="phone-input self-field" @keydown.enter.native="isPhone">
                <input @input="showPhone()" v-model="phone.call" class="self-field-core" type="tel" maxlength="11" placeholder="请输入手机号码">
            </mt-field>
            <i :class="{'icon': callEmpty, 'clear-icon':!callEmpty }" @click="addres"></i>
            <div class="info-wrap" :class="{'error': error}">
                <p v-text="phone.info"></p>
                <div class="phone-list" v-if="(filterList.length > 0) && showPhoneList">
                    <div class="cover"></div>
                    <cell @click.native="setPhone(item)" v-for="item in filterList" :key="item.call" class="phone-item" :title="item.call" :label="item.info"></cell>
                    <div class="clear">
                        清空历史纪录
                    </div>
                </div>
            </div>
        </div>
        <div class="phone-amount">
            <div class="cell-top">
                选择充值金额
            </div>
            <div class="amount-pannel-group">
                <div v-for="amount in amounts" class="amount-pannel" :class="{'active': active === amount.id}" @click="selectAmount(amount)">
                    <p class="val">{{amount.val}}</p>
                    <p class="desc">{{amount.desc}}</p>
                </div>
            </div>
        </div>

        <div class="field-reminder">
            <h4>
                温馨提示：
            </h4>
            <p>
                1、业务缴费受理时间为01:00-23:00,请您合理安排缴费时间，由此带来的不便，敬请谅解！
            </p>
            <p>
                2、通过电子渠道缴费不支持发票凭证打印功能。
            </p>
        </div>

        <div class="field-action button-bottom">
            <mt-button type="primary" :disabled="!clickable.phone || !clickable.amount" size="large" @click="recharge">充值</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleIndex from '../module/index';
    import * as services from '@/services/life/index';
    export default {
        name: 'phone',
        data() {
            return {
                phone: {
                    call: '',
                    info: '', // 显示运营商（可能包括姓名）/错误信息
                    amount: ''
                },
                phoneList: [],
                showPhoneList: false,
                error: false,          // 错误提示
                active: '',         // 当前激活金额
                clickable: {
                    phone: false,
                    amount: false
                },   // 是否可以点击
                callEmpty: true,    // 手机输入框是否为空
                amounts: [
                    {
                        id: '30',
                        val: '30元',
                        desc: '售价30元'
                    }, {
                        id: '50',
                        val: '50元',
                        desc: '售价50元'
                    }, {
                        id: '100',
                        val: '100元',
                        desc: '售价100元'
                    }, {
                        id: '200',
                        val: '200元',
                        desc: '售价200元'
                    }, {
                        id: '300',
                        val: '300元',
                        desc: '售价300元'
                    }, {
                        id: '500',
                        val: '500元',
                        desc: '售价500元'
                    }
                ]
            };
        },
        computed: {
            filterList() {
                if (this.phone.call !== '') {
                    let _data = this.phoneList.filter(item => {
                        return item.call.indexOf(this.phone.call) !== -1;
                    });
                    return _data.length > 0 ? _data.slice(0, 3) : [];
                } else {
                    return [];
                }
            }
        },
        created() {
            let ls = this.$ls.getItem('phone');
            if (ls !== null && ls !== undefined) {
                this.phone.call = ls.call;
            }
            services.getPhoneList()
                .then(res => {
                    this.phoneList = res.data;
                }, err => {
                    console.log(err);
                });
        },
        watch: {
            'phone.call': {
                handler: 'validate',
                deep: true
            },
            'phone.amount': {
                handler: 'validateAmount',
                deep: true
            }
        },
        methods: {
            selectAmount(item) {
                this.phone.amount = item.id;
                this.active = item.id;
            },
            validate(val) {
                if (this.$validate.isEmpty(val)) {
                    this.clickable.phone = false;
                    this.callEmpty = true;
                    return false;
                } else {
                    this.callEmpty = false;
                }
            },
            validateAmount(val) {
                if (this.$validate.isEmpty(val)) {
                    this.clickable.amount = false;
                    return false;
                }
                this.clickable.amount = true;
            },
            isPhone() {
                this.showPhoneList = false;
                if (!this.$validate.isPhone(this.phone.call)) {
                    this.clickable.phone = false;
                    this.phone.info = '请输入正确的手机号码';
                    this.error = true;
                    return false;
                } else {
                    this.phone.info = '中国移动';
                    this.clickable.phone = true;
                    this.error = false;
                }
            },
            recharge() {
                this.$router.push({ name: 'LifePhonePay' });
            },
            addres() {
                if (this.callEmpty) {
                    this.$msg('调用系统通讯录', 'mintui mintui-success');
                } else {
                    this.phone.call = "";
                    this.phone.info = "";
                }
            },
            showPhone() {
                this.error = false;
                this.showPhoneList = true;
            },
            setPhone(item) {
                this.phone.call = item.call;
                this.phone.info = item.info;
                this.showPhoneList = false;
                this.clickable.phone = true;
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$ls.setItem('phone', this.phone);
            } else {
                this.$ls.removeItem('phone');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .field-group {
        position: relative;
        margin-bottom: 40px;
        background-color: #fff;

         :-ms-input-placeholder {
            color: #b5b5b5;
            font-size: 16px;
        }

         ::-webkit-input-placeholder {
            color: #b5b5b5;
            font-size: 16px;
        }

        .phone-input {
            height: 146px;
        }

        .icon {
            position: absolute;
            top: 50px;
            right: 40px;
            height: 50px;
            width: 50px;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            @include image-set('../../../assets/img/selector/contacts');
        }

        .clear-icon {
            position: absolute;
            top: 58px;
            right: 48px;
            height: 34px;
            width: 34px;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            @include image-set('../../../assets/img/mint-ui/icon-input-clear');
        }

        .info-wrap {
            position: relative;
            height: 76px;
            padding: 20px 50px;

            .phone-list {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                z-index: 3;

                .cover {
                    position: fixed;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    top: 150px;
                    background-color: rgba(0, 0, 0, .6)
                }

                .phone-item .cell {
                    height: 144px;
                }

                .clear {
                    position: relative;
                    background-color: #fff;
                    text-align: center;
                    height: 100px;
                    line-height: 100px;
                    color: #308CF8;



                    &::after {
                        left: 50px;
                    }
                }
            }
        }

        .error p {
            color: #FF511A;
        }
    }

    .phone-amount {
        position: relative;
        background-color: #fff;
        padding: 30px 50px;

        .cell-top {
            font-size: 13px;
            color: #666666;
            letter-spacing: 0;
        }

        .amount-pannel-group {
            margin-top: 14px;

            .amount-pannel {
                display: inline-block;
                margin: 14px 1%;
                padding: 14px 44px;
                width: 31.333%;
                text-align: center;
                border: 1px solid #ddd;
                border-radius: 3px;

                .val {
                    font-size: 16px;
                    color: #272727;
                }

                .desc {
                    margin-top: 10px;
                    font-size: 13px;
                    color: #666666;
                    letter-spacing: 0;
                }
            }
            p {
                white-space: nowrap;
            }
        }

        .active {
            border: 1px solid #FF942F!important;

            .val {
                color: #FF942F!important;
            }

            .desc {
                color: #FF942F!important;
            }
        }
    }
</style>


