<template>
    <div class="television-info">
        <module-info ref="child" module="television"></module-info>

        <div class="field-action button-bottom">
            <mt-button type="primary" size="large" @click="pay">立即交费</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleInfo from '../module/info';
    export default {
        name: 'television-info',
        components: {
            ModuleInfo
        },
        mounted() {
            this.$refs.child.map[0].title = "有线电视公司";
        },
        methods: {
            pay() {
                let history = this.$route.query.history;
                this.$router.push({ name: 'LifeTelevisionPay', query: { history: history } });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('television');
            }
            next(true);
        }
    };

</script>


