<template>
    <div class="msg">
        <div class="wrap">
            <div class="page-icon"></div>
            <div class="page-title" v-text="title"></div>
            <div class="page-msg" v-html="msg"></div>
        </div>

        <div class="field-action fix-bottom">
            <mt-button type="primary" size="large" @click="submit">完成</mt-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'msg',
        data() {
            return {
                title: '无需缴费',
                msg: '账户已缴清或未出帐'
            };
        },
        methods: {
            submit() {
                this.$router.go(-2);
            }
        }
    };

</script>

<style lang="scss" scoped>
    .wrap {
        padding-top: 260px;
        text-align: center;

        .page-icon {
            width: 140px;
            height: 180px;
            margin: 0 auto;
            @include image-set('../../../assets/img/life/noPay');
        }

        .page-title {
            margin-top: 40px;
            font-size: 17px;
            color: #666;
        }

        .page-msg {
            margin-top: 10px;
            color: #b5b5b5;
        }
    }

    .fix-bottom {
        position: fixed;
        bottom: 20px;
        width: 100%;
    }
</style>


